import Trash from '@/apis/trash'
import { Message } from 'element-ui'

const state = {
    trashNotes: null,
    curTrashNoteId: null
}

const getters = {
    trashNotes: state => state.trashNotes || [], //从state里拿到transhNotes，没有就返回空数组

    curTrashNote: (state, getters) => {
        console.log(getters.transhNotes)
        if (!state.curTrashNoteId) return getters.trashNotes[0] || {}
        return state.trashNotes.find(note => note.id == state.curTrashNoteId) || {}
    },
    belongTo: (state,getters,rootState,rootGetters) => {
        let notebook = rootGetters.notebooks.find(notebook => notebook.id == getters.curTrashNote.notebookId) || {}
        return notebook.title || ''
    }
}

const mutations = {
    setTrashNotes(state, payload) {  //设置transhNotes
        state.trashNotes = payload.trashNotes
    },
    addTrashNote(state, payload) {   //回收站添加一个
        setTrashNotes.unshift(payload.note)
    },
    deleteTrashNotes(state, payload) {     //回收删除,过滤出note.id != payload.noteId的
        
        state.trashNotes = state.trashNotes.filter(note => note.id != payload.noteId)
    },

    setCurTrashNote(state, payload = {}) {  //设置curTrashNoteId
        state.curTrashNoteId = payload.curTrashNoteId
    }
}

const actions = {
    getTrashNotes({ commit }) {
        return Trash.getAll() //获取放到回收站的笔记
            .then(res => {   //获取成功后提交一次
                commit('setTrashNotes', { trashNotes: res.data })   //将res.data赋值给trashNotes
            })
    },
    deleteTrashNote({ commit }, { noteId }) { //删除
        return Trash.deleteNote({ noteId })
            .then(res => {
                commit('deleteTrashNotes', { noteId })
                Message.success(res.msg)  //删除成功提示
            })
    },

    revertTrashNote({ commit }, { noteId }) {
        return Trash.revertNote({ noteId })
            .then(res => {
                commit('deleteTrashNotes', { noteId })
                Message.success(res.msg)  //删除成功提示
            })
    }


}



export default {
    state,
    getters,
    mutations,
    actions
}


