<template>
  <div id="trash" class="detail">
    <div class="note-sidebar">
      <h3 class="notebook-title">回收站</h3>
      <!-- placement="bottom"  菜单提示在底部 -->

      <div class="menu">
        <div>更新时间</div>
        <div>标题</div>
      </div>
      <ul class="notes">
        <li v-for="note in trashNotes" :key="note.id">
          <router-link :to="`/trash?noteId=${note.id}`">
            <span class="data">{{ note.updatedAtFriendly }}</span>
            <span class="title">{{ note.title }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="note-detail">
      <div class="note-bar" v-if="true">
        <span>创建日期：{{ curTrashNote.createdAtFriendly }}</span>
        <span>|</span>
        <span>更新日期：{{ curTrashNote.updatedAtFriendly }}</span>
        <span>|</span>
        <span>所属笔记:{{ belongTo }}</span>
        <a class="btn action" @click="onRevert">恢复</a>
        <a class="btn action" @click="onDelete">彻底删除</a>
      </div>
      <div class="note-title">
        <span>{{ curTrashNote.title }}</span>
      </div>
      <div class="editor">
        <div class="preview markdown-body" v-html="compiledMarkdown"></div>
      </div>
    </div>
  </div>
</template>

<script>

import MarkdownIt from "markdown-it";
let md = new MarkdownIt();
import { mapState, mapActions, mapGetters, mapMutations } from "vuex";

export default {
  data() {
    return {
      // curTrashNote:{},
    
    };
  },

  computed: {    //拿到getters里的页面所有内容
    ...mapGetters(["trashNotes", "curTrashNote","belongTo"]),

    compiledMarkdown() {
      return md.render(this.curTrashNote.content || "");
    },
  },

  methods: {
    ...mapMutations(["setCurTrashNote"]),
    ...mapActions([
      "checkLogin",
      "revertTrashNote",
      "deleteTrashNote",
      "getTrashNotes",
      "getNotebooks"
    ]),

    onRevert() {
      this.revertTrashNote({ noteId: this.curTrashNote.id })
      .then(() => {
      this.setCurTrashNote({});  //默认选中第一个
      this.$router.replace({
        path:'/trash',   //回收站默认选中第一个
        query:{noteId:this.curTrashNote.id}
      })
        });
    },
    onDelete() {
      this.$confirm('删除后将无法恢复, 是否继续?', '确定删除?', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        return  this.deleteTrashNote({ noteId: this.curTrashNote.id })
        .then(() => {
         this.setCurTrashNote()
      this.$router.replace({
        path:'/trash',
        query:{        //进入页面自动选中
          noteId:this.curTrashNote.id,
        
        }
      })
      })
      })
      
    },
  },

  created() {
    this.checkLogin({ path: "/login" }); //判断用户是否登录
    this.getNotebooks()
    this.getTrashNotes().then(() => {
      this.setCurTrashNote({ curTrashNoteId: this.$route.query.noteId });
      this.$router.replace({
        path:'/trash',   //回收站默认选中第一个
        query:{noteId:this.curTrashNote.id}
      })
    });

    // Auth.getInfo()
    // .then(res => {
    //   if(!res.isLogin) {   //如果用户未登录时跳转到登录页面
    //     this.$router.push({ path: '/login' })
    //   }
    // })
  },

  beforeRouteUpdate(to, from, next) {
    this.setCurTrashNote({ curTrashNoteId: to.query.noteId });
    next();
  },
};
</script>

<style lang="less" scoped>
#trash {
  display: flex;
  // align-items: stretch;
  background-color: #fff;
  flex: 1;

  .note-bar {
    .action {
      float: right;
      margin-left: 10px;
      padding: 2px 4px;
      font-size: 12px;
    }
  }
}

.note-sidebar {
  position: relative;
  width: 300px;
  border-right: 1px solid #ccc;
  background-color: #eee;

  .add-note {
    position: absolute;
    right: 5px;
    top: 12px;
    color: #666;
    font-size: 12px;
    padding: 2px 4px;
    box-shadow: 0px 0px 2px 0px #ccc;
    border: none;
    z-index: 1;
  }

  .notebook-title {
    font-size: 18px;
    font-weight: normal;
    color: #333;
    height: 45px;
    line-height: 45px;
    text-align: center;
    border-bottom: 1px solid #ccc;
    background-color: #f7f7f7;
    display: block;
  }
  .el-dropdown-link {
    cursor: pointer;
  }
  .el-dropdown-menu__item {
    width: 200px;
  }

  .menu {
    display: flex;

    div {
      font-size: 12px;
      padding: 2px 10px;
      flex: 1;
      border-right: 1px solid #ccc;
      border-bottom: 1px solid #ccc;

      &:last-child {
        border-right: none;
      }
    }

    .iconfont {
      font-size: 10px;
    }
  }

  .notes {
    li {
      &:nth-child(odd) {
        background-color: #f2f2f2;
      }

      a {
        display: flex;
        padding: 3px 0;
        font-size: 12px;
        border: 2px solid transparent;
      }

      .router-link-exact-active {
        border: 2px solid #81c0f3;
        border-radius: 3px;
      }

      span {
        padding: 0 10px;
        flex: 1;
      }
    }
  }
}

#note {
  display: flex;
  align-items: stretch; //对齐项目:拉伸
  background-color: #fff;
  flex: 1;
}

.note-detail {
  flex: 1;
  display: flex;
  flex-direction: column; //方向，按列排

  .note-detail-ct {
    height: 100%;
  }

  .note-empty {
    font-size: 50px;
    color: #ccc;
    text-align: center;
    margin-top: 100px;
  }

  .note-bar {
    padding: 10px 30px;
    border-bottom: 1px solid #eee;
    font-weight: bold;
    margin-right: 5px;
    &:after {
      content: "";
      display: block;

      clear: both;
    }
    span {
      font-size: 12px;
      color: #999;
      margin-right: 4px;
    }
    .iconfont {
      float: right;
      margin-left: 10px;
      font-size: 18px;
      cursor: pointer;
    }
  }

  .note-title {
    input,
    span {
      display: inline-block;
      width: 100%;
      border: none;
      outline: none;
      font-size: 18px;
      padding: 10px 20px;
    }
  }

  .editor {
    height: ~"calc(100% - 70px)";
    position: relative;

    .vue-codemirror {
      height: ~"calc(100% - 25px)";

      .CodeMirror {
        height: 100%;
        padding: 10px;
      }
    }
  }
  textarea,
  .preview {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 20px;
  }

  textarea {
    border: none;
    resize: none;
    outline: none;
    font-size: 14px;
    font-family: "Monaco", courier, monospace;
  }

  code {
    color: #f66;
  }
}
</style>


